* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.jumbotron {
  position: relative;
  background-color: rgba(0, 0, 0, .45) !important;
}

.jumbotron-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-image: url('sklep.jpg');
  background-position: center;
  background-size: cover;
  z-index: -1;
}

.nav-logo {
  width: 8%;
}

.contact-logo {
  width: 25vw;
}

.page-hight-100 {
  height: 80vh;
}

/* SEKCJA KONTAKT */
.form-contact {
  text-align: center;
}

.form-label {
  display: block;
  padding: 5px 0;
}

.form-input {
  margin: 0 5px;
  padding: 2px;
}

.form-area {
  margin: 10px auto;
  padding: 2px;
}

.red {
  color: crimson;
}

.green {
  color: green;
}